<template>
  <div class="change-bank-card">
    <div>
      <el-steps :active="active" finish-status="success" align-center>
        <el-step title="填写银行卡信息"></el-step>
        <el-step title="变更银行卡"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <div class="tip">重要提示：银行卡变更后，请注意认证状态，以免影响后续放款和提现操作。</div>
      <el-row class="list">
        <el-col :span="24">
          <div>
            华夏存管账户：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input1" placeholder="无"></el-input>
          </div>
          <div>
            存管子帐号(蜡笔)：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input2" placeholder="无"></el-input>
          </div>
          <div>
            账户类型：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input3" placeholder="无"></el-input>
          </div>
          <div>
            银行卡号码：<el-select style="width: 30%" v-model="value" placeholder="请选择" :disabled="active === 2">
            <el-option v-for="(item, index) in bankList"
              :key="index"
              :label="item.bankCardNo"
              :value="item.bankCardNo">
            </el-option>
          </el-select>
          </div>
          <div>
            开户行：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input4" placeholder="无"></el-input>
          </div>
        </el-col>
      </el-row>
      <el-table
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          prop="mingcheng"
          label="企业名称" show-overflow-tooltip
          align="center">
        </el-table-column>
        <el-table-column
          prop="daima"
          label="组织机构代码" show-overflow-tooltip
          align="center">
        </el-table-column>
        <el-table-column
          prop="bianhao"
          label="营业执照编号"
          align="center">
        </el-table-column>
        <el-table-column
          prop="dengjihao"
          label="税务登记号"
          align="center">
        </el-table-column>
        <el-table-column
          prop="faren"
          label="法人姓名"
          align="center">
        </el-table-column>
        <el-table-column
          prop="shenfenzheng"
          label="法人身份证号"
          align="center">
        </el-table-column>
      </el-table>
      <el-button type="primary" style="margin-top: 30px;" @click="nowChangeBank"  v-if="active === 2">立即变更</el-button>
      <el-button style="margin-top: 30px;" @click="next">{{nextText}}</el-button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from 'axios'
import APIURL from '@/config/APIURL'
Axios.defaults.timeout = 5000
Axios.defaults.withCredentials = true
Axios.defaults.headers.post['Content-Type'] = 'application/json; charset=utf-8'
Vue.prototype.$ajax = Axios
export default {
  name: 'change-bank-card',
  data () {
    return {
      active: 1,
      nextText: '下一步',
      tableData: [{
        mingcheng: '',
        daima: '',
        bianhao: '',
        dengjihao: '',
        faren: '',
        shenfenzheng: ''
      }],
      input1: '',
      input2: '',
      input3: '',
      input4: '',
      value: '',
      AcctInfo: '',
      bankList: ''
    }
  },
  created () {
    this.getBankList()
    this.AcctInfo = JSON.parse(sessionStorage.getItem('AcctInfo'))
    this.input1 = this.AcctInfo.depositAcct
    this.input2 = this.AcctInfo.subAcct
    this.input3 = this.AcctInfo.businessTypeMsg
    this.tableData[0].mingcheng = this.AcctInfo.orgName
    this.tableData[0].daima = this.AcctInfo.certNo
    this.tableData[0].bianhao = this.AcctInfo.busiLiceNo
    this.tableData[0].dengjihao = this.AcctInfo.taxNo
    this.tableData[0].faren = this.AcctInfo.legalName
    this.tableData[0].shenfenzheng = this.AcctInfo.legalCertNo
  },
  methods: {
    next () {
      // if (this.value) {
      this.active++
      // } else {
      //   alert('请选择银行卡号')
      // }
      if (this.active === 2) {
        this.nextText = '上一步'
      } else if (this.active > 2) {
        this.active = 1
        this.nextText = '下一步'
      }
    },
    getNowFormatDate () {
      let date = new Date()
      let seperator1 = '-'
      let seperator2 = ':'
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    },
    // 获取银行卡列表
    getBankList () {
      const getBankListUrl = APIURL.getBankList
      this.$ajax.post(getBankListUrl, {
        appId: 'org_crm_merchant',
        loginPhone: sessionStorage.getItem('phone'),
        recoCode: sessionStorage.getItem('recoCode'),
        reqTime: this.getNowFormatDate(),
        token: sessionStorage.getItem('token')
      }).then((res) => {
        if (res.data.code === 0) {
          this.bankList = res.data.data
        } else {
          alert(res.data.msg)
        }
      })
    },
    nowChangeBank () {
      const updateBankUrl = APIURL.updateBank
      this.$ajax.post(updateBankUrl, {
        appId: 'org_crm_merchant',
        loginPhone: sessionStorage.getItem('phone'),
        bankCardNo: this.value,
        recoCode: sessionStorage.getItem('recoCode'),
        reqTime: this.getNowFormatDate(),
        token: sessionStorage.getItem('token')
      }).then((res) => {
        if (res.data.code === 0) {
          location.href = res.data.data.jumpUrl
        } else {
          alert(res.data.msg)
        }
      })
    }
  },
  watch: {
    value: function (newValue, oldValue) {
      this.bankList.forEach((item, index) => {
        if (newValue === this.bankList[index].bankCardNo) {
          this.input4 = this.bankList[index].bankType
        }
      })
    }
  },
  computed: {
  },
  components: {
  }
}
</script>
<style lang="scss" scoped>
  .change-bank-card{
    div{
      line-height: 1.2;
      text-align: right;
    }
    .el-steps{
      padding: 20px 0;
    }
    .tip{
      width: 80%;
      height: 30px;
      background-color: #1890ff;
      color: #fff;
      margin: 0 auto;
      text-align: center;
      line-height: 30px;
    }
    .list{
      div{
        width: 80%;
        margin-top: 5px;
      }
    }
    .el-table{
      margin-top: 30px;
    }
    .el-button{
      display: block;
      margin: 0 auto;
    }
  }
</style>



